'use client';

interface ArticleContentProps {
  content: string;
}

export default function ArticleContent({ content }: ArticleContentProps) {
  return (
    <div className="prose prose-lg max-w-none">
      {content.split('\n').map((paragraph, index) => {
        if (paragraph.startsWith('# ')) {
          return (
            <h1 key={index} className="text-3xl font-bold mt-8 mb-4 text-gray-900">
              {paragraph.replace('# ', '')}
            </h1>
          );
        }
        if (paragraph.startsWith('## ')) {
          return (
            <h2 key={index} className="text-2xl font-bold mt-6 mb-3 text-gray-900">
              {paragraph.replace('## ', '')}
            </h2>
          );
        }
        if (paragraph.startsWith('**') && paragraph.endsWith('**')) {
          return (
            <p key={index} className="font-bold mb-4 text-gray-900">
              {paragraph.replace(/\*\*/g, '')}
            </p>
          );
        }
        if (paragraph.match(/^\d+\./)) {
          return (
            <li key={index} className="ml-6 mb-2 text-gray-800">
              <span dangerouslySetInnerHTML={{ __html: paragraph.replace(/^\d+\.\s*/, '') }} />
            </li>
          );
        }
        if (paragraph.startsWith('- ')) {
          return (
            <li key={index} className="ml-6 mb-2 text-gray-800">
              <span dangerouslySetInnerHTML={{ __html: paragraph.replace('- ', '') }} />
            </li>
          );
        }
        if (paragraph.trim() === '') {
          return <br key={index} />;
        }

        // Render paragraph with HTML (for internal links)
        return (
          <p 
            key={index} 
            className="mb-4 text-gray-800 leading-relaxed text-base"
            dangerouslySetInnerHTML={{ __html: paragraph }}
          />
        );
      })}
    </div>
  );
}
